import React, { useState, useEffect, useMemo, useRef } from "react";
import { useParams, Link } from "react-router-dom";

// 1.   useMe,  fetchMe    
import { useMe } from "../../api/auth";
import AdminChatWindow from "./AdminChatWindow";
import { fetchAdminMessages, sendAdminMessage } from "../../api/admin";

const WELCOME =
  " !    , " +
  ",   ,     .";
const POLL_DELAY = 2000; //    

export default function AdminChatPage() {
  const { orderId } = useParams();

  // --- 2.    ,    useEffect ---
  const { data: admin, isLoading: isAdminLoading, isError } = useMe();

  const [messages, setMsgs] = useState([]);
  const [loading, setLoading] = useState(true); //    
  const [error, setError] = useState("");
  const [pollError, setPollError] = useState(false);

  const lastSeenRef = useRef("");

  // --- 3.  useEffect      ---
  useEffect(() => {
    //   ,       ID 
    if (!admin || !orderId) {
      return;
    }

    const controller = new AbortController();
    let isCancelled = false;

    // ---     ---
    const loadInitialMessages = async () => {
      try {
        if (isCancelled) return;
        setLoading(true);

        let msgs = await fetchAdminMessages(
          orderId,
          "",
          false,
          controller.signal,
        );

        //   ,   
        if (!msgs.length) {
          const welcome = await sendAdminMessage(orderId, WELCOME);
          msgs = [welcome];
        }

        if (!isCancelled) {
          setMsgs(msgs);
          //      long-polling
          lastSeenRef.current = msgs.at(-1)?.created_at || "";
        }
      } catch (err) {
        if (!isCancelled) {
          const status = err.response?.status;
          setError(
            status === 404 ? "  " : "   ",
          );
        }
      } finally {
        if (!isCancelled) setLoading(false);
      }
    };

    // --- Long-polling     ---
    const pollNewMessages = async () => {
      while (!isCancelled) {
        try {
          const news = await fetchAdminMessages(
            orderId,
            lastSeenRef.current,
            true, // usePoll = true
            controller.signal,
          );

          if (news.length && !isCancelled) {
            setMsgs((prev) => {
              const known = new Set(prev.map((m) => m.id));
              const uniqueNews = news.filter((n) => !known.has(n.id));
              if (uniqueNews.length === 0) return prev;
              return [...prev, ...uniqueNews];
            });
            lastSeenRef.current = news.at(-1).created_at;
          }
          if (!isCancelled) setPollError(false);
        } catch (err) {
          if (!isCancelled && err.name !== "AbortError") {
            setPollError(true);
          }
        }
        await new Promise((r) => setTimeout(r, POLL_DELAY));
      }
    };

    //  :   ,   
    loadInitialMessages().then(() => {
      if (!isCancelled) {
        pollNewMessages();
      }
    });

    //   
    return () => {
      isCancelled = true;
      controller.abort();
    };
  }, [orderId, admin]); //   admin  orderId

  //     
  const handleSend = async (text) => {
    if (!admin) return;

    const tmpId = `tmp-${Date.now()}`;
    const tmpMsg = {
      id: tmpId,
      content: text,
      user_id: admin.id,
      is_admin: true,
      created_at: new Date().toISOString(),
      pending: true,
    };
    setMsgs((prev) => [...prev, tmpMsg]);

    try {
      const real = await sendAdminMessage(orderId, text, tmpId);
      setMsgs((prev) => prev.map((m) => (m.id === tmpId ? real : m)));
      lastSeenRef.current = real.created_at;
    } catch {
      setError("   ");
      setMsgs((prev) => prev.filter((m) => m.id !== tmpId));
    }
  };

  //    /  
  const display = useMemo(() => {
    const out = [];
    let lastIsAdmin = null;
    messages.forEach((m, i) => {
      const isAdm = m.is_admin ?? m.user_id === admin?.id;
      if (lastIsAdmin === null || isAdm !== lastIsAdmin) {
        out.push({
          id: `div-${i}`,
          type: "divider",
          label: isAdm ? "" : "",
        });
        lastIsAdmin = isAdm;
      }
      out.push(m);
    });
    return out;
  }, [messages, admin]);

  // --- 4.  UI- ---
  if (isAdminLoading)
    return <div style={{ padding: 20 }}> </div>;
  if (isError)
    return (
      <div style={{ padding: 20 }}>
            
      </div>
    );

  if (loading) return <div style={{ padding: 20 }}> </div>;
  if (error) {
    return (
      <div style={{ padding: 20 }}>
        <Link to="/admin/messages">   </Link>
        <div style={{ color: "red", marginTop: 10 }}>{error}</div>
      </div>
    );
  }

  return (
    <div
      style={{
        padding: 20,
        display: "flex",
        flexDirection: "column",
        height: "calc(100vh - 40px)",
      }}
    >
      <Link to="/admin/messages">   </Link>
      <h2 style={{ display: "flex", alignItems: "center", gap: 8 }}>
          #{orderId}
        {pollError && (
          <span style={{ color: "#d94747", fontSize: 14 }}>  </span>
        )}
      </h2>

      {admin ? (
        <AdminChatWindow messages={display} admin={admin} onSend={handleSend} />
      ) : (
        <div> </div>
      )}
    </div>
  );
}
