// src/services/auth.js

import axios from "axios";
import { useQuery } from "@tanstack/react-query";

// 
// 0. Axios instance
// 
//  VITE_API_BASE_URL  .env.[mode]  (, https://api.example.com)
//  dev     "/api".
const api = axios.create({
  baseURL: import.meta.env.VITE_API_BASE_URL || "/api",
});

// 
// 1.   initData (storage helpers)
// 
const INIT_KEY = "tg_init_data"; // sessionStorage,    

export function getInitData() {
  return sessionStorage.getItem(INIT_KEY);
}

export function setInitData(raw) {
  sessionStorage.setItem(INIT_KEY, raw);
}

export function clearInitData() {
  sessionStorage.removeItem(INIT_KEY);
}

// 
// 2. Axios interceptors
// 
api.interceptors.request.use(
  (config) => {
    const init = getInitData();
    if (init) config.headers["X-Telegram-Init-Data"] = init;
    return config;
  },
  (error) => Promise.reject(error),
);

api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      // invalid / expired initData   storage,  ReactQuery  /me
      clearInitData();
    }
    return Promise.reject(error);
  },
);

// 
// 3. Auth API helpers
// 
/**
 *   Telegram Web App.
 *  `initData` =>   .
 * @param {string} initData   Telegram.WebApp.initData (raw)
 * @returns {Promise<object>} user
 */
export async function loginWithTelegram(initData) {
  const { data } = await api.post("/auth/login", { init_data: initData });
  setInitData(initData); //    
  return data.user; // backend  { user: {  } }
}

/**
 *         ,   initData.
 */
export function logout() {
  clearInitData();
}

/**
 * Current user (    ReactQuery)
 */
export async function fetchMe() {
  const { data } = await api.get("/auth/me");
  return data; // backend  UserSchema
}

/**
 * Reacthook,    /me,   initData.
 */
export function useMe() {
  const enabled = Boolean(getInitData());
  return useQuery({ queryKey: ["me"], queryFn: fetchMe, enabled });
}

// 
// 4. Utils
// 
export function isAuthenticated() {
  return Boolean(getInitData());
}

// Default export    ( )
export default api;
