import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

// 1.   useMe    
import { useMe } from "../api/auth";
import { fetchUserChats } from "../api/messages";
import styles from "./ChatListPage.module.css";

export default function ChatListPage() {
  /*  state  */
  const [chats, setChats] = useState([]);
  const [loading, setLoad] = useState(true);
  const [error, setError] = useState("");

  const nav = useNavigate();

  // 2.      React Query
  const { isSuccess: isUserLoaded, isLoading: isUserLoading } = useMe();

  /*     */
  useEffect(() => {
    // 3.  ,   isUserLoaded  true
    if (isUserLoaded) {
      (async () => {
        try {
          //  loading  true ,    
          setLoad(true);
          const list = await fetchUserChats();
          setChats(list);
        } catch (e) {
          console.error("   :", e);
          setError(e.message || "   ");
        } finally {
          setLoad(false);
        }
      })();
    }
  }, [isUserLoaded]); // 4.  isUserLoaded   useEffect

  /*  UI-  */
  //    ,   
  if (isUserLoading)
    return <div className={styles.placeholder}> </div>;

  //   ,    
  if (loading) return <div className={styles.placeholder}> </div>;

  if (error) return <div className={styles.placeholder}>{error}</div>;
  if (!chats.length)
    return <div className={styles.placeholder}>  </div>;

  /*     */
  return (
    <div className={styles.container}>
      {chats.map(({ order_id, product_title, content, created_at }) => {
        const go = () => nav(`/messages/${order_id}`);
        const time = created_at ? new Date(created_at).toLocaleString() : "";
        const snippet = content?.trim()
          ? content.length > 80
            ? content.slice(0, 80) + ""
            : content
          : "  ";

        return (
          <div
            key={order_id}
            role="button"
            tabIndex={0}
            className={styles.card}
            onClick={go}
            onKeyDown={(e) => e.key === "Enter" && go()}
          >
            <div className={styles.top}>
              <span className={styles.order}>&nbsp;#{order_id}</span>
              <span className={styles.time}>{time}</span>
            </div>
            <div className={styles.title}>
              {product_title || " "}
            </div>
            <div className={styles.snippet}>{snippet}</div>
          </div>
        );
      })}
    </div>
  );
}
