import React, { useEffect, useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useQueryClient } from "@tanstack/react-query";
import { loginWithTelegram } from "../api/auth";
import styles from "./LoginPage.module.css";

const log = (...args) => console.log("[LoginPage]", ...args);
const logError = (...args) => console.error("[LoginPage]", ...args);

export default function LoginPage() {
  const [error, setError] = useState(null);
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const qc = useQueryClient();

  useEffect(() => {
    log("  ,  useEffect  .");

    qc.clear();
    log("  React Query .");

    console.group(" [LoginPage]   ");

    let rawInit;
    if (window.Telegram?.WebApp?.initData) {
      rawInit = window.Telegram.WebApp.initData;
    } else {
      rawInit = searchParams.get("initData");
    }

    if (!rawInit) {
      setError("     Telegram (initData).");
      console.groupEnd();
      return;
    }

    log(" 1:   initData.");
    log(" 2:  `loginWithTelegram`...");

    loginWithTelegram(rawInit)
      .then((user) => {
        //    user  
        log(" 3:     auth.js.");
        console.log("    :", user);

        // ---    ---
        // ,  'user'        id
        if (!user || typeof user !== "object" || !user.id) {
          logError(
            "   :       .",
          );
          throw new Error("      .");
        }

        log("    :", user);

        log(" 4:      React Query...");
        qc.setQueryData(["me"], user);
        log("        .");

        log(" 5:     '/'...");
        navigate("/", { replace: true });
      })
      .catch((e) => {
        logError("   [LoginPage]    :", e);
        const msg =
          e.response?.data?.detail || e.response?.data?.message || e.message;
        setError(msg);
      })
      .finally(() => {
        log("   .");
        console.groupEnd();
      });
  }, [qc, navigate, searchParams]);

  return (
    <div className={styles.page}>
      {error ? (
        <div className={styles.error}> : {error}</div>
      ) : (
        <div className={styles.loading}>  Telegram</div>
      )}
    </div>
  );
}
