// src/pages/admin/NewProductPage.jsx
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import styles from "./NewProductPage.module.css";
import { createAdminProduct } from "../../api/admin";

// 1.   useMe
import { useMe } from "../../api/auth";

export default function NewProductPage() {
  const [form, setForm] = useState({
    title: "",
    description: "",
    price: "",
    image_url: "",
  });
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState("");
  const navigate = useNavigate();

  // 2.   
  const { isSuccess: isUserReady, isLoading: isUserLoading } = useMe();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((f) => ({ ...f, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSubmitting(true);
    setError("");
    try {
      await createAdminProduct(form);
      alert("  ");
      navigate("/admin/products");
    } catch (err) {
      console.error(err);
      setError("   : " + err.message);
    } finally {
      setSubmitting(false);
    }
  };

  // 3.       
  const getSubmitButtonState = () => {
    if (isUserLoading) return { text: " ", disabled: true };
    if (submitting) return { text: "", disabled: true };
    if (!isUserReady) return { text: "", disabled: true };
    return { text: "", disabled: false };
  };

  const buttonState = getSubmitButtonState();

  return (
    <div className={styles.container}>
      <form className={styles.form} onSubmit={handleSubmit}>
        <h2 className={styles.heading}> </h2>

        {error && <div className={styles.error}>{error}</div>}

        <label className={styles.label}>
          
          <input
            name="title"
            className={styles.input}
            value={form.title}
            onChange={handleChange}
            required
          />
        </label>

        <label className={styles.label}>
          
          <textarea
            name="description"
            className={styles.textarea}
            value={form.description}
            onChange={handleChange}
            required
          />
        </label>

        <label className={styles.label}>
           ()
          <input
            name="price"
            type="number"
            className={styles.input}
            value={form.price}
            onChange={handleChange}
            required
          />
        </label>

        <label className={styles.label}>
          URL 
          <input
            name="image_url"
            className={styles.input}
            value={form.image_url}
            onChange={handleChange}
            required
          />
        </label>

        <div className={styles.buttons}>
          <button
            type="submit"
            className={styles.submitButton}
            disabled={buttonState.disabled}
          >
            {buttonState.text}
          </button>
          <button
            type="button"
            className={styles.cancelButton}
            onClick={() => navigate("/admin/products")}
            disabled={submitting}
          >
            
          </button>
        </div>
      </form>
    </div>
  );
}
